
h1{
  color: white;
  font-family: 'Poppins',sans-serif;
  font-weight: 100;
  font-size: 50px;
}
.app{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 40px 24px;
}
.app__searchform{
  margin-top: 18px;
}
.app__input{
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 15px;
}
.app__search{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: #02aff0 ;
  border: 2px solid #02aff0 ;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}
.app__search:hover{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: white ;
  border: 2px solid #02aff0 ;
  border-radius: 4px;
  color: #02aff0;
  cursor: pointer;
}

.recipetile__img{
  height: 200px;
  width: 200px;
  object-fit: cover;
  border-radius: 4px;

  
}

.recipetile__name{
  max-width: 200px;
  text-align: center;
  margin: 12px 0px;
}
.app__recipes{
  margin-top: 24px;
  display: grid;
  grid-template-columns: 280px 280px 280px 235px;
  align-items: baseline;
  cursor: pointer;
}
.app__healthlabels{
  padding: 7px 8px;
  margin-left: 6px;
  border-radius: 4px ;
  font-family: 'Poppins',sans-serif;
}

.recipetile{
  color: white;
}